<template>
  <div class="banner">
    <div class="box">
      <ul class="tests">
        <p v-for="(item,index) of list" :key="index">{{item}}</p>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      list: [
        '打了死结的青春，捆死一颗苍白绝望的灵魂。',
        '为自己掘一个坟墓来葬心，红尘一梦，不再追寻。',
        '加了锁的青春，不会再因谁而推开心门。'
      ]
    }
  }
}
</script>

<style scoped>
.banner{
  background: url(../../assets/banner_top.jpg) top center;
  height: 265px;
  overflow: hidden;
}
.box{
  width: 1000px;
  margin: auto;
  overflow: hidden;
}
.tests{
  width: 350px;
  line-height: 26px;
  margin: 60px 0 0 20px;
  float: left;
  font-size: 14px
}
.tests p{
  transform: translate(60px);
  text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.8), 2px 2px 3px rgba(180, 151, 151, 0.3);
}
</style>



