<template>
  <div class="head">
    <div class="logo">
      <a></a>
    </div>
    <topnav></topnav>
  </div>
</template>
<script>
import topnav from './nav.vue'
export default {
  components: {
    topnav
  }
}
</script>

<style scoped>
.head{
  width: 1000px;
  margin: auto;
  height: 80px;
  position: relative;
  overflow: hidden;
}
.logo a{
  width: 260px;
  height: 60px;
  margin: 10px 0 0 0;
  position: absolute;
  background: url(../../assets/tit.jpg) no-repeat;
  display: block;
}
</style>



