<template>
  <div class="nav">
    <a v-for="(item,index) of navlist" :key="index">{{item.name}}</a>
  </div>
</template>
<script>
export default {
  data () {
    return {
      navlist: [
        {name: '首页'},
        {name: '首页'},
        {name: '首页'},
        {name: '首页'},
        {name: '首页'},
        {name: '首页'}
      ]
    }
  }
}
</script>
<style scoped>
.nav{
  float: right;
    width: 100%;
    margin: 30px 0 0 0;
    text-align: right;
}
.nav a{
  position: relative;
  display: inline-block;
  font-size: 18px;
  margin: 0 5px;
  padding: 0 8px;
}
</style>



